import React from 'react';

import ModuleSection from './ModuleSection';
import Feature from './Feature';
import Skills from './Skills';
import FAQ from './FAQ';
import CourseTop from './CourseTop';
import FixedContent from './FixedContent';

const CourseSection = () => {
  return (
    <div className="container mx-auto flex flex-col space-y-10 leading-tight py-16 text-gray-700 px-5 sm:px-10">
      <div className="flex flex-col md:flex-row justify-between mx-auto px-5 lg:px-10 text-grey">
        <div className="pb-10 md:w-4/5 md:pr-4 max-w-3xl w-full">
          <CourseTop />
          <ModuleSection />
          <Feature />
          <Skills />
          <FAQ />
        </div>
        <div className="hidden md:block md:w-1/5 ml-4">
          <FixedContent />
        </div>
      </div>
    </div>
  );
};

export default CourseSection;
