import React, { useState } from 'react';
import JavaImg from '../../../../assets/Java.webp';

const ModuleContent = ({ moduleNumber, moduleTitle, moduleDescription, lessons, imgSrc }) => {
  const [isContentVisible, setContentVisible] = useState(false);

  const handleToggle = () => {
    setContentVisible(!isContentVisible);
  };

  return (
    <div className="flex flex-col lg:flex-row border border-grey-500 bg-grey-200 bg-opacity-75 select-none cursor-pointer">
      <div className="flex items-center justify-between lg:hidden px-4 py-2" onClick={handleToggle}>
        <p className="text-orange font-semibold">{moduleNumber}</p>
        <svg
          xmlns="http://www.w3.org/2000/svg"
          width={35}
          height={24}
          viewBox="0 0 24 24"
          fill="none"
          stroke="currentColor"
          strokeWidth={2}
          strokeLinecap="round"
          strokeLinejoin="round"
          className={`text-grey transition-transform duration-300 ${isContentVisible ? 'rotate-180' : ''}`}
        >
          <polyline points="6 9 12 15 18 9" />
        </svg>
      </div>
      <div className="lg:flex py-4 lg:pl-5 pr-3 lg:pr-0 lg:space-x-5 lg:items-start">
        <div className="flex flex-col lg:items-center lg:flex-shrink-0">
          <div className="w-20 md:p-0 hidden lg:block">
            <img
              src={imgSrc}
              className="hidden lg:block"
              alt={moduleTitle}
              width="80px"
              height="80px"
            />
          </div>
          <p className="hidden lg:block text-orange font-semibold">{moduleNumber}</p>
        </div>
        <div className="flex flex-col lg:w-full">
          <div className="flex flex-col text-grey space-y-3 leading-tight">
            <div className="flex lg:flex-row flex-col items-start lg:items-center space-y-1 lg:space-y-0 lg:space-x-2">
              <h3 className="text-black font-semibold text-lg">{moduleTitle}</h3>
            </div>
            <div className="relative flex flex-col items-start">
              <div className="overflow-hidden" style={{ height: "auto" }}>
                {moduleDescription}
              </div>
            </div>
          </div>
          <div
            className={`flex flex-col transition-all duration-1000 overflow-hidden ${isContentVisible ? 'max-h-screen' : 'max-h-0'}`}
          >
            <div className="flex flex-col mt-5">
              {lessons.map((lesson, index) => (
                <div key={index} className="py-4 flex space-x-3 border-t border-grey-400 lessonContainer">
                  <div className="flex flex-col items-start space-y-3 leading-tight">
                    <div className="flex lg:flex-row flex-col lg:items-center">
                      <p className="text-lg font-semibold text-grey">{lesson.title}</p>
                    </div>
                    <p>{lesson.description}</p>
                  </div>
                </div>
              ))}
            </div>
          </div>
        </div>
        <div className="hidden lg:flex pt-10 pr-5 order" onClick={handleToggle}>
          <svg
            xmlns="http://www.w3.org/2000/svg"
            width={35}
            height={24}
            viewBox="0 0 24 24"
            fill="none"
            stroke="currentColor"
            strokeWidth={2}
            strokeLinecap="round"
            strokeLinejoin="round"
            className={`text-grey transition-transform duration-300 ${isContentVisible ? 'rotate-180' : ''}`}
          >
            <polyline points="6 9 12 15 18 9" />
          </svg>
        </div>
      </div>
    </div>
  );
};

const ModuleList = () => {
  const modules = [
    {
      moduleNumber: 'Module 1',
      moduleTitle: 'HTML Basics',
      moduleDescription:
        'Learn the fundamentals of HTML, including tags, attributes, and semantic HTML. Build basic web pages and understand the structure of web documents.',
      imgSrc: JavaImg,
      lessons: [
        { title: 'Lesson 1: Introduction to HTML', description: 'Learn about the history of HTML, its purpose, and basic structure.' },
        { title: 'Lesson 2: HTML Tags and Attributes', description: 'Explore various HTML tags and attributes used in web development.' },
        { title: 'Lesson 3: Semantic HTML', description: 'Understand the importance of semantic HTML and how it affects accessibility and SEO.' },
        { title: 'Lesson 4: Building Your First Web Page', description: 'Practical session on creating a simple web page using HTML.' },
      ],
    },
    {
      moduleNumber: 'Module 2',
      moduleTitle: 'CSS Fundamentals',
      moduleDescription:
        'Explore CSS properties, selectors, and styles. Learn to apply CSS for layout design, typography, and responsive web design.',
      imgSrc: JavaImg,
      lessons: [
        { title: 'Lesson 1: Introduction to CSS', description: 'Learn about CSS, its purpose, and how it complements HTML in web development.' },
        { title: 'Lesson 2: CSS Selectors and Properties', description: 'Understand various CSS selectors and properties used to style HTML elements.' },
        { title: 'Lesson 3: Layout Design with CSS', description: 'Explore different layout techniques using CSS, including flexbox and grid.' },
        { title: 'Lesson 4: Responsive Web Design', description: 'Learn to create responsive web pages that adapt to different screen sizes and devices.' },
      ],
    },
    {
      moduleNumber: 'Module 3',
      moduleTitle: 'JavaScript Essentials',
      moduleDescription:
        'Get started with JavaScript programming. Learn about variables, functions, control structures, and DOM manipulation for dynamic web pages.',
      imgSrc: JavaImg,
      lessons: [
        { title: 'Lesson 1: Introduction to JavaScript', description: 'Understand the role of JavaScript in web development and its core features.' },
        { title: 'Lesson 2: Variables and Data Types', description: 'Learn about variables, data types, and type coercion in JavaScript.' },
        { title: 'Lesson 3: Functions and Scope', description: 'Explore function declarations, expressions, and variable scope in JavaScript.' },
        { title: 'Lesson 4: DOM Manipulation', description: 'Learn to interact with the Document Object Model (DOM) using JavaScript.' },
      ],
    },
    {
      moduleNumber: 'Module 4',
      moduleTitle: 'React Basics',
      moduleDescription:
        'Introduction to React. Learn about components, JSX, state management with useState, and effects with useEffect.',
      imgSrc: JavaImg,
      lessons: [
        { title: 'Lesson 1: Introduction to React', description: 'Understand the fundamentals of React, including components and JSX syntax.' },
        { title: 'Lesson 2: State and Props', description: 'Learn to manage state and pass data between components using props in React.' },
      ],
    },
    {
      moduleNumber: 'Module 5',
      moduleTitle: 'React Hooks',
      moduleDescription:
        'Explore React Hooks for functional components. Learn to use useState, useEffect, and custom hooks for managing state and side effects.',
      imgSrc: JavaImg,
      lessons: [
        { title: 'Lesson 1: useState Hook', description: 'Manage component state with the useState hook.' },
        { title: 'Lesson 2: useEffect Hook', description: 'Handle side effects in functional components with the useEffect hook.' },
        { title: 'Lesson 3: Custom Hooks', description: 'Create and use custom hooks to reuse stateful logic across components.' },
      ],
    },
    {
      moduleNumber: 'Module 6',
      moduleTitle: 'React Router',
      moduleDescription:
        'Implement routing in React applications using React Router. Learn about Route, Link, useParams, and useHistory.',
      imgSrc: JavaImg,
      lessons: [
        { title: 'Lesson 1: Introduction to React Router', description: 'Understand the basics of routing and navigation in single-page applications.' },
        { title: 'Lesson 2: Route Parameters', description: 'Pass and retrieve parameters in route URLs using useParams.' },
        { title: 'Lesson 3: Navigation with Link', description: 'Navigate between different routes using Link components.' },
        { title: 'Lesson 4: Programmatic Navigation', description: 'Redirect users programmatically using useHistory.' },
      ],
    },
  ];
  return (
    <div>
      {modules.map((module, index) => (
        <ModuleContent
          key={index}
          moduleNumber={module.moduleNumber}
          moduleTitle={module.moduleTitle}
          moduleDescription={module.moduleDescription}
          lessons={module.lessons}
          imgSrc={module.imgSrc}
        />
      ))}
    </div>
  );
};

export default ModuleList;
