import React, { useState } from 'react';

const CourseTop = () => {
    const [isExpanded, setIsExpanded] = useState(false);

    const toggleExpansion = () => {
        setIsExpanded(!isExpanded);
    };

    return (
        <div className="flex flex-col text-grey justify-center">
            <h2 className="text-2xl font-bold mb-4 leading-tight text-center">
                What is React Development Training?
            </h2>
            <div
                className="text-lg overflow-hidden transition-all duration-700"
                style={{ maxHeight: isExpanded ? 'none' : '80px' }}
            >
                <p className="text-justify">
                    React Development training focuses on teaching learners how to build interactive user interfaces for web applications using React, a popular JavaScript library maintained by Facebook.
                </p>
                <p className="text-justify mt-4">
                    This course covers essential topics such as React components, JSX syntax, state management using hooks (useState, useEffect), routing with React Router, handling forms and user inputs, integrating with RESTful APIs for data fetching, and deploying React applications.
                </p>
                <p className="text-justify mt-4">
                    Students will learn modern front-end development practices, including responsive design, accessibility considerations, and best practices for performance optimization in React applications.
                </p>
                <p className="text-justify mt-4">
                    By the end of the course, learners will be proficient in building scalable and maintainable web applications using React, prepared for roles as front-end developers, UI/UX engineers, or React developers in software development teams.
                </p>
            </div>
            <button
                className="underline text-left mt-3 focus:outline-none hover:text-orange self-center"
                onClick={toggleExpansion}
            >
                {isExpanded ? 'Show less' : 'Show more'}
            </button>
        </div>
    );
};

export default CourseTop;
