import React, { useState } from 'react';
import { FaChevronDown, FaChevronUp } from 'react-icons/fa'; // Ensure you have react-icons installed

const FAQ = () => {
  const [openFAQ, setOpenFAQ] = useState(null);

  const toggleFAQ = (index) => {
    setOpenFAQ(openFAQ === index ? null : index);
  };

  const faqs = [
    {
      question: "What is React Development?",
      answer: "React Development refers to building user interfaces for web applications using React, a JavaScript library developed by Facebook. It allows developers to create reusable UI components and manage application state efficiently.",
    },
    {
      question: "Why choose React for web development?",
      answer: "React is popular for web development due to its component-based architecture, virtual DOM for efficient rendering, one-way data flow with Flux/Redux for state management, and strong community support with a rich ecosystem of libraries and tools.",
    },
    {
      question: "What are the key features of React?",
      answer: "Key features of React include JSX syntax for writing components, component lifecycle methods, hooks like useState and useEffect for managing state and side effects, virtual DOM for optimizing performance, and server-side rendering (SSR) for SEO-friendly applications.",
    },
    {
      question: "How does React handle data binding?",
      answer: "React uses uni-directional data flow and props to pass data from parent to child components. State management with hooks like useState allows components to manage their own state independently, ensuring predictable and efficient data binding.",
    },
    {
      question: "What are the benefits of using Redux with React?",
      answer: "Redux is a predictable state container for JavaScript applications, commonly used with React for managing global application state. It facilitates centralized state management, time-travel debugging, and simplifies testing of React components.",
    },
    {
      question: "What are React hooks?",
      answer: "React hooks are functions that let developers use state and other React features without writing classes. useState manages component state, useEffect performs side effects in functional components, useContext accesses React context, and more.",
    },
    {
      question: "How does React support server-side rendering (SSR)?",
      answer: "React supports server-side rendering (SSR) to pre-render components on the server and send HTML to the client. SSR improves initial load time, SEO, and provides a better user experience by delivering fully rendered pages to the browser.",
    },
    {
      question: "What are React Router and its advantages?",
      answer: "React Router is a popular routing library for React applications, enabling navigation between different views/components. It provides declarative routing, nested route configuration, dynamic route matching, and browser history management.",
    },
    {
      question: "How can React applications be optimized for performance?",
      answer: "React application performance can be optimized by minimizing component renders using shouldComponentUpdate or React.memo, lazy loading components with React.lazy and Suspense, code splitting for bundling optimization, and using tools like Chrome DevTools for profiling.",
    },
    {
      question: "What are the career opportunities for React developers?",
      answer: "React developers are in high demand for roles such as Front-end Developer, UI/UX Engineer, Full Stack Developer, React Developer, and Technical Lead. With skills in React and related technologies, developers can work across various industries on innovative web projects.",
    },
  ];

  return (
    <div className="container mx-auto mt-16">
      <h2 className="text-center text-xl md:text-3xl font-bold mb-10 leading-tight">
        Frequently Asked Questions about React Development
      </h2>
      <div className="flex flex-col gap-4">
        {faqs.map((faq, index) => (
          <section key={index} className="border-b pb-2">
            <div
              className="flex justify-between items-center cursor-pointer"
              onClick={() => toggleFAQ(index)}
            >
              <h3 className="font-semibold text-lg">{faq.question}</h3>
              {openFAQ === index ? <FaChevronUp /> : <FaChevronDown />}
            </div>
            {openFAQ === index && <p className="mt-2">{faq.answer}</p>}
          </section>
        ))}
      </div>
    </div>
  );
};

export default FAQ;
